<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择景区 start -->
         <el-form-item label="" prop="scenicSpotName">
			<el-select
			  v-model="searchValidateForm.scenicSpotId"
			  filterable
			  placeholder="请选择景区"
			  clearable
			>
			  <el-option
			    v-for="(item, index) in spotList"
			    :key="item.value"
			    :label="item.scenicSpotName"
			    :value="item.scenicSpotId"
			  >
			  </el-option>
			</el-select>
            </el-input>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请选择奖池类型 start -->
         <el-form-item label="" prop="prizeName">
            <el-select
              v-model="searchValidateForm.prizeId"
              filterable
              placeholder="请选择奖品"
              clearable
            >
              <el-option
                v-for="(item, index) in prizeList"
                :key="item.value"
                :label="item.treasureName"
                :value="item.treasureId"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择奖池类型 end -->

			<!-- 请选择兑换端口 start -->
			<el-form-item label="" prop="exchangePort">
			   <el-select
			     v-model="searchValidateForm.exchangePort"
			     filterable
			     placeholder="请选择兑换端口"
			     clearable
			   >
			     <el-option
			       v-for="(item, index) in exchangePortList"
			       :key="item.value"
			       :label="item.label"
			       :value="item.value"
			     >
			     </el-option>
			   </el-select>
			 </el-form-item>
			 <!-- 请选择兑换端口 end -->
			 
			 <!-- 请选择操作状态 start -->
			 <el-form-item label="" prop="operationalState">
			    <el-select
			      v-model="searchValidateForm.operationalState"
			      filterable
			      placeholder="请选择操作状态"
			      clearable
			    >
			      <el-option
			        v-for="(item, index) in operationalStateList"
			        :key="item.value"
			        :label="item.label"
			        :value="item.value"
			      >
			      </el-option>
			    </el-select>
			  </el-form-item>
			  <!-- 请选择操作状态 end -->
			  
			  <!-- 请选择操作状态 start -->
			  <el-form-item label="" prop="operationalState">
					<el-input v-model="searchValidateForm.userPhone1" placeholder="请输入用户手机号" />
			   </el-form-item>
			   <!-- 请选择操作状态 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 查询数据 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

      </el-header>

      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>

      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { createUrl,formatDate } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

