	      label: '后台管理系统',
	    },
	    {
	      value: '3',
	      label: '微信小程序',
	    },
	  ],
	  jackpotTypeList:[
		  {
		    value: '1',
		    label: '定点寻宝',
		  },
		  {
		    value: '2',
		    label: '定时寻宝',
		  },
	  ],
	  jackpotId:'',
	  dialogVisible:false,
	  PrizecurrentPage:1,
	  PrizePageSize:10,
