 //    this.getFactoryFour()
	// this.getoddNumber()
    this.initTableData()
  },
  beforeMount() {},
  mounted() {},
  methods: {
	  async getPrizeList(){
		  const { data: res } = await this.$http.post(
		      '/system/broadcastHunt/getTreasureNewHunt',{
		  		pageNum: this.currentPage, // 第几页
		  		pageSize: this.pageSize // 每页多少条
		  	})
			console.log(res.data.list)
		  this.prizeList = res.data.list
	  },
	  beforeUpload(categories,file){
		   this.file = categories
		     this.$message({
		             message: '上传成功',
		             type: 'success'
		           });
	  },
	  // 点击删除时触发
	  deleteRow(index, row) {
	    // console.log(index, row);
	    this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
	      confirmButtonText: '确定',
	      cancelButtonText: '取消',
	      type: 'warning'
	    })
	      .then(async () => {
	        // let data = this.$qs.stringify({
	        //   id: row.id
	        // })
	        const { data: res } = await this.$http.post(
	          '/system/broadcastHunt/delectJackpotNew',
	          {
	            jackpotId: row.jackpotId
	          }
	        )
	        // console.log(res);
	        if (res.state == '200') {
	          this.$message({
	            type: 'success',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 1000
	          })
	          this.initTableData()
	        } else {
	          this.$message({
	            type: 'error',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 3000
	          })
	        }
	      })
	      .catch(() => {
	        this.$message({
	          type: 'info',
	          message: '已取消删除',
	          offset: 100,
	          center: true,
	          duration: 1000
	        })
	      })
	  },
	  cancelClick(){
		  this.drawer = false
		  this.validateForm = {}
	  },
	  cancelClickPrize(){
		  this.drawerPrize = false
		  this.validateFormPrize = {}
	  },
	  // handleClose(done) {
