	  //               this.loading = false;
	  //             }, 400);
	  //           }, 2000);
	  //         })
	  //         .catch(_ => {});
	  //     },
	  async getSpotList(){
		  const { data: res } = await this.$http.get('/system/broadcastHunt/getSpotId')
		  if(res.state == 200){
			  this.spotList = res.data
		  }
		  console.log(res)
	  },
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
        goodId: null,
        spotId: null,
        inStockReason: ''
      }
    },
	
	getSearchValidateFormPrize() {
	  var validateForm = {}
	  for (var item in this.tableFields) {
	    validateForm[this.tableFields[item]] = ''
	  }
	  this.searchValidateFormPrize = {}
	},

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
    },

    /**
     * 初始化表格数据
     * @params - isPage - 是否分页
     */
    async initTableData(isPage = false) {
      this.tableLoading = true

	const { data: res } = await this.$http.post(
        '/system/broadcastHunt/getUserExchangeLog',{
			...this.searchValidateForm,
			pageNum: this.currentPage, // 第几页
			pageSize: this.pageSize // 每页多少条
		})
      console.log(res.data);
      if (res.state == '200') {
        // this.tableData = res.data
        // this.total = res.total
		this.tableData = res.data.list
		this.total = res.data.total
        this.tableLoading = false
      }
    },

//查看奖品
	handlePrize(index, row){
		this.jackpotId = row.jackpotId
		this.dialogVisible = !this.dialogVisible
		// this.getPrizeList(this.jackpotId)
	},

	// async getPrizeList(jackpotId){
	// 	const { data: res } = await this.$http.post(
	// 	  '/system/broadcastHunt/getTreasureNewHunt',{
	// 		  ...this.searchValidateFormPrize,
	// 		  jackpotId:jackpotId,
	// 		  pageNum:this.PrizecurrentPage,
	// 		  pageSize:this.PrizePageSize,
	// 	})
	// 	console.log(res)
	// 	// if(prizeData)
	// 	this.PrizeTotalPage = res.data.total
	// 	this.prizeData = res.data.list
	// },
    /**
     * 更改确认/撤销状态
     * @param id - 当前行数据id
     * @param status - 当前行数据status(0、初始状态；1、确认；2、撤销)
     */
    async updateStatus(id, status) {
      const { data: res } = await this.$http.post(
        '/system/inventory_detail/updateStatus',
        this.$qs.stringify({
          id: id,
          status: status // 0、初始状态；1、确认；2、撤销
        })
      )
      this.initTableData(true)
    },

    /**
     * 根据单号查询数据(预览)
     */
    async getByOrderNumber() {
      if (!this.previewValidateForm.previewOrderNumber) {
        this.previewValidateForm.previewOrderNumber = null
      }

      const { data: res } = await this.$http.get(
        '/system/inventory_detail/getByOrderNumber',
        {
          params: {
            orderNumber: this.previewValidateForm.previewOrderNumber,
            type: 1 // 1、入库；2、出库
          }
        }
      )
      return res
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      var startDate =
        (this.searchStartEndDate && this.searchStartEndDate[0]) || null
      var endDate =
        (this.searchStartEndDate && this.searchStartEndDate[1]) || null

      // console.log(startDate, endDate)

      const { data: res } = await this.$http.get(
        '/system/inventory_detail/downloadExcel',
        {
          params: {
            ...this.searchValidateForm,
            startDate,
            endDate,
            type: 1 // 1、入库；2、出库
          },
          responseType: 'arraybuffer' // 这里必须制定响应的数据类型为arraybuffer，否则导出的文件无法打开
        }
      )
      if (res) {
        const url = window.URL.createObjectURL(
          new Blob([res], {
            type:
              'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
          })
        )
        const a = document.createElement('a')
        console.log(url)
        a.href = url
        a.download = '入库信息'
        a.click()

        this.$message({
          message: '导出成功',
          type: 'success'
        })
      } else {
        this.$message({
          message: '导入失败',
          type: 'error'
        })
      }

      // var params = {
      //   ...this.searchValidateForm,
      //   url: '/system/inventory_detail/downloadExcel',
      //   startDate,
      //   endDate,
      //   type: 1 // 1、入库；2、出库
      // }
      // var downloadUrl = createUrl(params)
      // this.downloadUrl = downloadUrl
      // this.$nextTick(() => {
      //   var exportAEle = document.getElementById('exportA')
      //   exportAEle.click()
      // })
      // const { data: res } = await this.$http.get("/system/inventory_detail/list", {
      //   params: {
      //     startDate: beginTime,
      //     endDate: endTime,
      //     spotId: spotId,
      //     goodId: goodId,
      //     orderNumber: orderNumber,
      //     type: 1, // 1、入库；2、出库
      //   }
      // })
      // if (res.state == "200") {
      //   this.exportTableData = res.data.list
      // } else {
      //   return
      // }
    },

    // 导入EXCEL
    async importE(file) {
      this.importLoading = true
      var formData = new FormData()
      formData.append('file', file)

      const { data: res } = await this.$http.post(
        '/system/inventory_detail/importExcel',
        formData
      )
      this.importLoading = false
      if (res.state == '200') {
        this.$message({
          message: '导入成功！',
          type: 'success'
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error'
        })
      }
    },

    // 点击当前行确认时触发
    confirmRow(index, row) {
      this.$confirm('确认已入库?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.updateStatus(row.id, 1)
          this.$message({
            type: 'success',
            message: '确认成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消确认操作'
          })
        })
    },

