  components: {
    // DownloadExcel: JsonExcel,
    // StockList
  },
  computed: {
	  setTreasuretype(){
		  return (state) =>{
			  var result = ''
			  if (state == '1') {
			    result = '代金卷'
			  } else if (state == '2') {
			    result = '免单'
			  } else if(state == '3'){
			    result = '实物'
			  }else{
			  	result = '谢谢参与'
			  }
			  return result
		  }
	  },
	  setPrizeSize(){
		  return (state) =>{
		  			  var result = ''
		  			 if (state == '0') {
		  			   result = '普通奖品'
		  			 } else if (state == '1') {
		  			   result = '大奖'
		  			 } else {
		  			   result = '暂无'
		  			 }
		  			  return result
		  }
	  },
	  setProbability(){
		  return (state) =>{
				var result = state.split("-")
				console.log(result)
				if(result[1] == 9){
					return parseFloat(state).toFixed(9) + '%'
				}else if(result[1] == 8){
					return parseFloat(state).toFixed(8) + '%'
				}else if(result[1] == 7){
					return parseFloat(state).toFixed(7) + '%'
				}else if(result[1] == 6){
					return parseFloat(state).toFixed(6) + '%'
				}else if(result[1] == 5){
					return parseFloat(state).toFixed(5) + '%'
				}else if(result[1] == 4){
					return parseFloat(state).toFixed(4) + '%'
				}else {
					var arr = parseFloat(state).toFixed(10)
					return parseFloat(arr) + '%'
				}
		  }
	  },
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value);
        var result = ''
        if (value == 'exchangePort') {
          if (row[value] == 1) {
            result = '管理者APP'
          } else if (row[value] == 2) {
            result = '后台管理系统'
          } else if (row[value] == 3) {
            result = '微信小程序'
          }
        } else if (value == 'operationalState') {
          if (row[value] == 0) {
            result = '绑定地址'
          } else if (row[value] == 1) {
            result = '已发货'
          }else if(row[value] == 2) {
			   result = '已收货'
		  }else if(row[value] == 3){
			   result = '现场兑换'
		  }
        } else if (value == 'inStockType') {
          if (row[value] == 1) {
            result = '采购'
          } else if (row[value] == 2) {
            result = '回收'
          }
        } else if(value == 'jackpotType'){
			if(row[value] == 1){
				 result = '定点寻宝'
			}else if(row[value] == 2){
				 result = '定时寻宝'
			}
		}else {
          result = row[value] || '无'
        }
        return result
      }
    }
  }
}
</script>

